@import "../../../public/libs/jquery-ui/jquery-ui.min";
@import "../../../public/libs/jcf/css/jcf.custom";
@import "../../../public/libs/product-tour/product-tour.min";

.visual:before{
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 225px;
  background: linear-gradient(to bottom,rgba(0,0,0,0.2) 0,rgba(255,255,255,0) 100%);
  pointer-events: none;
}

.visual .caption {
  padding: 184px 0 78px;
}

@media (max-width: 767px) {
  .visual .caption {
    padding: 90px 0;
  }
}

.visual .container,
.visual .row,
.visual .col-xs-12 {
  height: 100%;
}

@media (max-width: 767px) {
  .visual .container,
  .visual .row,
  .visual .col-xs-12 {
    height: auto;
  }
}

.visual img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}

.visual .offer-block {
  margin: 0 0 0 -9999px;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 10px 10px 5px 9999px;
  background: rgba(255, 255, 255, 0.7);
  position: absolute;
  left: 0;
  top: auto;
  right: auto;
  bottom: 0;
}

@media (max-width: 767px) {
  .visual .offer-block {
    margin: 0;
    padding: 10px 0;
    position: static;
    text-align: center;
  }
}

.visual .offer-block .box {
  width: 310px;
  overflow: hidden;
}

@media (max-width: 991px) {
  .visual .offer-block .box {
    width: 270px;
  }
}

@media (max-width: 767px) {
  .visual .offer-block .box {
    width: auto;
  }
}

.visual .offer-block .title {
  display: block;
  font-size: 32px;
  line-height: 36px;
  font-weight: 400;
}

@media (max-width: 991px) {
  .visual .offer-block .title {
    font-size: 26px;
    line-height: 30px;
    font-weight: 400;
  }
}

.visual .offer-block .sub-title {
  display: block;
  margin: 0 0 5px;
  text-transform: none;
  font-size: 25px;
  line-height: 29px;
  font-weight: 400;
}

@media (max-width: 991px) {
  .visual .offer-block .sub-title {
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
  }
}

.visual .offer-block .btn-success {
  float: right;
}

@media (max-width: 767px) {
  .visual .offer-block .btn-success {
    float: none;
  }
}

.visual .offer-block p {
  margin: 0;
}

@media (max-width: 767px) {
  .visual .offer-block p {
    margin: 0 0 10px;
  }
}

.activity-form {
  float: right;
  padding: 16px 17px 23px;
  background: rgba(41, 69, 106, 0.9);
}

@media (max-width: 991px) {
  .activity-form {
    width: 400px;
    max-width: 400px;
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .activity-form {
    width: auto;
    float: none;
    padding: 10px;
    max-height: inherit;
  }
}

.activity-form .jcf-select {
  border-radius: 4px;
  height: auto;
  border: 2px solid #A56C00;
  width: 280px;
  display: inline-block;
  vertical-align: top;
  margin-right: 10px;
}

@media (max-width: 991px){
  .activity-form .jcf-select {
    margin-bottom: 15px;
  }
}

.activity-form .jcf-select .jcf-select-text {
  color: #000;
  font-size: 15px;
  margin: 4px 11px;
}

.activity-form .jcf-select-opener{
  display: none;
}

.activity-form .form-control{
  padding: 4px 11px;
  height: auto;
  font-size: 15px;
  width: 120px;
}

.activity-form .title {
  color: #ffffff;
  display: block;
  margin: 4px 0 8px;
  font-size: 28px;
  line-height: 34px;
  font-family: "PT-Sans", sans-serif;
  font-weight: 700;
}

@media (max-width: 991px) {
  .activity-form .title {
    margin: 0 0 10px;
    font-size: 24px;
    line-height: 30px;
  }
}

@media (max-width: 767px) {
  .activity-form .title {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}

.activity-form .text-field {
  margin-right: 10px;
  overflow: hidden;
  background: #fff;
  position: relative;
  border-radius: 4px;
  border: 2px solid #a56c00;
  display: inline-block;
  vertical-align: top;
}


@media (max-width: 767px){
  .activity-form .text-field{
    margin-left: -3px;
  }
}

.activity-form .holder {
  overflow: hidden;
  font-size: 0;
}

.activity-form .col {
  width: 54.5%;
  margin: 0 0.5% 0;
  display: inline-block;
  vertical-align: top;
}

.activity-form .col.col-second{
  width: 42%
}

@media (max-width: 991px) {
  .activity-form .col {
    width: auto;
    display: block;
    margin: 0 -4px;
  }
  .activity-form .col:first-child {
    margin: 0;
  }

  .activity-form .col.col-second{
    width: auto;
  }

  .activity-form .sub-col{
    width: 61.3%;
  }

  .activity-form .sub-col.sub-col-second{
    width: 33%;
  }

}

.activity-form .sub-col {
  width: 54.3%;
  margin: 0 1% 0;
  display: inline-block;
  vertical-align: top;
}

.activity-form .sub-col.sub-col-second{
  width: 40%;
}

.activity-form .btn-primary {
  background: #E48A00;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
  font-weight: 700;
  padding: 6px 13px;
  vertical-align: top;
}

