/**********Best price section*************/

.best-price-section{
  background: #F2F2F2;
  padding-bottom: 38px;
}

.best-price-section__header{
  color: #0081CC;
  text-align: center;
  margin: 28px 0 14px;
}

.best-price-section__header-wrapper{
  display: inline-block;
  background-image: url('../images/sale.png');
  background-repeat: no-repeat;
  background-position: 0 4px;
  padding-left: 101px;
}

.best-price-section__title{
  font-size: 57px;
  font-weight: 700;
  margin: 0;
}

.best-price-section__title-description{
  font-size: 32px;
  margin: 0;
  line-height: 1;
}

.best-price-section__content{
  max-width: 750px;
  margin: 0 auto;
}

.best-price-section__summary{
  color: #535353;
  font-size: 24px;
  line-height: 1.2;
  text-align: center;
  margin: 0 0 34px;
}

.steps-list{
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0;
}

.steps-list__item{
  display: inline-block;
  vertical-align: top;
  width: 25%;
  text-align: center;
  position: relative;
}

.steps-list__item:after{
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  border: solid #e7e7e7;
  border-width: 3px 3px 0 0;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  right: -5px;
  top: 26px;
}

.steps-list__item:last-child:after{
  display: none;
}

.steps-list__figure{
  width: 82px;
  height: 82px;
  background-color: #006CAD;
  margin: 0 auto 16px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  text-align: center;
  position: relative;
}

.steps-list__icon{
  max-width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 36px;
  margin: auto;
}

.steps-list__figcaption{
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 10px;
  line-height: 1;
  margin: 0;
  font-size: 20px;
  color: #fff;
  font-weight: 700;
}

.steps-list__name{
  font-size: 24px;
  margin: 0;
  color: #313131;
  line-height: 1.2
}

.steps-list__bold-text{
  display: block;
}

@media screen and (max-width: 767px){

  .best-price-section__header-wrapper{
    padding-left: 0;
    background-image: none;
  }

  .best-price-section__title{
    font-size: 44px;
  }

  .best-price-section__title-description{
    font-size: 26px;
  }

  .best-price-section__summary{
    font-size: 22px;
  }

}

@media screen and (max-width: 580px){

  .best-price-section{
    padding-bottom: 0;
  }

  .best-price-section__title{
    font-size: 36px;
    margin-bottom: 10px;
  }

  .best-price-section__title-description{
    font-size: 20px;
  }

  .best-price-section__summary{
    font-size: 18px;
  }

  .steps-list__item{
    display: block;
    width: 100%;
    max-width: 200px;
    margin: 0 auto 70px;
  }

  .steps-list__item:after{
    top: auto;
    bottom: -41px;
    right: 0;
    left: 0;
    margin: 0 auto;
    border-width: 0 3px 3px 0;
  }

}

/**********END Best price section*************/


/**********Activities slider section*************/

.activities-slider-section{
  background: #fff;
  padding: 54px 0 24px;
}

.activities-slider-section__header{
  text-align: center;
  margin-bottom: 36px;
}

.activities-slider-section__title{
  font-weight: 700;
  font-size: 46px;
  color: #575757;
  margin: 0 0 6px;
  line-height: 1;
}

.activities-slider-section__sub-title{
  margin: 0;
  font-size: 20px;
  color: #747474;
  line-height: 1;
}



.activities-slider{
  position: relative;
}

.activities-slider__link,
.activities-slider__link:hover,
.activities-slider__link:focus,
.activities-slider__link:visited{
  color: #000;
}

.activities-slider__image{
  height: 184px;
  object-fit: cover;
  -o-object-fit: cover;
}

.activities-slider__name{
  font-weight: 700;
  margin: 14px 0 5px;
}

.activities-slider__description{
  margin: 0;
}

.activities-slider__arrow{
  width: 35px;
  height: 35px;
  background: #fff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  top: 87px;
  cursor: pointer;
}

.activities-slider__arrow:before{
  content: '';
  position: absolute;
  width: 12px;
  height: 12px;
  border: solid #303030;
  border-width: 0 0 3px 3px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}

.activities-slider__arrow_previous{
  left: -15px;
  -webkit-box-shadow: -3px 0 9px 0 rgba(0, 0, 0, 0.25);
  -moz-box-shadow: -3px 0 9px 0 rgba(0, 0, 0, 0.25);
  box-shadow: -3px 0 9px 0 rgba(0, 0, 0, 0.25);
}
.activities-slider__arrow_previous:before{
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.activities-slider__arrow_next {
  right: -15px;
  -webkit-box-shadow: 3px 0 9px 0 rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 3px 0 9px 0 rgba(0, 0, 0, 0.25);
  box-shadow: 3px 0 9px 0 rgba(0, 0, 0, 0.25);
}
.activities-slider__arrow_next:before{
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

@media screen and (max-width: 767px){

  .activities-slider-section__sub-title{
    font-size: 18px;
  }

  .activities-slider__image{
    height: 130px;
  }

  .activities-slider__arrow{
    top: 50px;
  }

  .activities-slider__arrow_previous{
    left: 0;
  }

  .activities-slider__arrow_next{
    right: 0;
  }
}

@media screen and (max-width: 579px){

  .activities-slider-section__title{
    font-size: 34px;
  }

  .activities-slider__name{
    font-size: 20px;
  }

}

@media screen and (max-width: 399px){

  .activities-slider-section__title{
    font-size: 26px;
  }

  .activities-slider__image{
    height: 180px;
  }

  .activities-slider__arrow{
    top: 80px;
  }

}

/**********END Activities slider section*************/



